<template>
  <el-card class="box-card">
    <el-form :inline="true" class="demo-form-inline">
      <el-form-item label="iname">
        <el-input placeholder="iname" v-model="param.iname"></el-input>
      </el-form-item>
      <el-form-item label="hover">
        <el-input placeholder="hover" v-model="param.hover"></el-input>
      </el-form-item>
      <el-form-item label="amountAvailable">
        <el-input placeholder="amountAvailable" v-model="param.amountAvailable"></el-input>
      </el-form-item>
      <el-form-item label="lowPrice">
        <el-input placeholder="lowPrice" v-model="param.lowPrice"></el-input>
      </el-form-item>
      <el-form-item label="highPrice">
        <el-input placeholder="highPrice" v-model="param.highPrice"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="searchForm">Query</el-button>
      </el-form-item>
    </el-form>
    <el-table
        border
        ref="multipleTable"
        :data="tableList"
        tooltip-effect="dark"
        style="width: 100%"
    >
      <el-table-column
          align="center"
          label="iname"
          prop="iname"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          label="hover"
          prop="hover"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          label="amountAvailable"
          prop="amountAvailable"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          label="lowPrice"
          prop="lowPrice"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          label="highPrice"
          prop="highPrice"
          sortable
      >
      </el-table-column>
    </el-table>
    <el-pagination
        background
        layout="prev, pager, next"
        :total="total"
        @current-change="changePage"
    >
    </el-pagination>
  </el-card>
</template>

<script>
export default {
  data() {
    return {
      param: {
        iname: '',
        hover: '',
        amountAvailable: '',
        lowPrice: '',
        highPrice: '',
      },
      tableList: [],
      pageInfo: {
        pageNum: 1,
        pageSize: 10,
      },
      total: 0
    }
  },
  mounted() {
    this.getTableList(this.pageInfo)
  },
  methods: {
    getTableList() {
      this.$axios.post('/displayIngredientView/findAll', Object.assign(this.param, this.pageInfo)).then(res => {
        this.tableList = res.data.rows;
        this.total = res.data.count;
      })
    },
    searchForm() {
      this.pageInfo.pageNum = 1;
      this.getTableList();
    },
    changePage(e) {
      this.pageInfo.pageNum = e
      this.getTableList()
    }
  },
}
</script>

<style scoped lang="scss">
.el-pagination {
  margin-top: 10px;
}
</style>

